.faq {

   &__block {
   }

   &__row {
      padding: 174px 0;
      @media(max-width: 1023px) {
         padding: 65px 25px 100px;
      }
      @media(max-width: 424px) {
         padding: 65px 25px 70px;
      }
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
      text-align: center;
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      color: #3D4F9F;
      padding: 20px 0 92px;
      @media(max-width: 1023px) {
         padding: 20px 0 50px;
      }
      @media(max-width: 424px) {
         font-size: 20px;
         padding: 10px 0 25px;
      }
   }
}
.faq-colapse {
   width: 743px;
   margin: 0 auto;
   @media(max-width: 1023px) {
      width: auto;
   }
   &__row {
      position: relative;
      &::after {
         content: '';
         display: block;
         width: 100%;
         height: 1px;
         background: #D8E3FE;
         bottom: 0;
         
      }
   }

   &__head {
      display: flex;
      padding: 15px 15px;
      border-top: 1px solid #D8E3FE;
      cursor: pointer;
      &.active {
         .faq-colapse__icon {
            transition: 0.5s;
            transform: rotate(180deg);
         }
      }
   }

   &__icon {
      transform: rotate(0deg);
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      line-height: 19px;
      display: flex;
      align-items: center;
      letter-spacing: 0.03em;
      
      color: #495274;
      
      padding: 0 0 0 24px;
   }

   &__body {
      display: none;
      transition: 0.5s;
      justify-content: space-between;
      align-items: center;
      padding: 0 15px 54px 80px;
      @media(max-width: 1023px) {
         flex-direction: column;
         align-items: normal;
         gap: 15px;
      }
      @media(max-width: 424px) {
         padding: 0 15px 25px 15px;
      }
   }

   &__text {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      letter-spacing: 0.03em;
      width: 391px;
      color: #8D96BD;
      @media(max-width: 1023px) {
         width: auto;
      }
   }

   &__button {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 13px;
      line-height: 16px;
      
      /* identical to box height */
      display: flex;
      align-items: center;
      justify-content: center;
      
      color: #7382A3;
      background: #E8ECF4;
      border-radius: 2px;
      width: 195px;
      height: 43px;
      cursor: pointer;
   }
}
