.logos {

   &__block {
   }

   &__row {
      display: flex;
      justify-content: center;
      gap: 76px;
      flex-wrap: wrap;
      padding: 58px 0;
      @media(max-width: 374px) {
         padding: 58px 15px;
      }
   }

   &__item {
      width: 116.58px;
      height: 27px;
   }

   &__item1 {
      background: url('../img/logos/logos1.png') 0 0 / 100% 100% no-repeat;
      &:hover {
         background: url('../img/logos/fillLogos1.png') 0 0 / 100% 100% no-repeat;
      }
   }

   &__item2 {
      background: url('../img/logos/logos2.png') 0 0 / 100% 100% no-repeat;
      &:hover {
         background: url('../img/logos/fillLogos2.png') 0 0 / 100% 100% no-repeat;
      }
   }

   &__item3 {
      background: url('../img/logos/logos3.png') 0 0 / 100% 100% no-repeat;
      &:hover {
         background: url('../img/logos/fillLogos3.png') 0 0 / 100% 100% no-repeat;
      }
   }

   &__item4 {
      background: url('../img/logos/logos4.png') 0 0 / 100% 100% no-repeat;
      &:hover {
         background: url('../img/logos/fillLogos4.png') 0 0 / 100% 100% no-repeat;
      }
   }

   &__item5 {
      background: url('../img/logos/logos5.png') 0 0 / 100% 100% no-repeat;
      &:hover {
         background: url('../img/logos/fillLogos5.png') 0 0 / 100% 100% no-repeat;
      }
   }
}
