.plan {

   &__block {
      background: #F5F8FF;
      position: relative;
   }

   &__row {
      padding: 208px 0;
      @media(max-width: 1023px) {
         padding: 80px 25px 400px;
      }
      @media(max-width: 767px) {
         padding: 80px 25px 350px;
      }
      @media(max-width: 424px) {
         padding: 80px 25px 300px;
      }
      @media(max-width: 374px) {
         padding: 80px 25px 280px;
      }
   }

   &__text {
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 300;
      font-size: 36px;
      line-height: 45px;
      color: #3D4F9F;
      padding: 27px 0;
      span {
         font-weight: 500;
      }
      @media(max-width: 374px) {
         font-size: 32px;
         padding: 20px 0;
      }
   }

   &__des {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 29px;
      letter-spacing: 0.03em;
      color: #8D96BD;
      padding: 0 0 47px 0;
      width: 413px;
      @media(max-width: 767px) {
         width: auto;
      }
      @media(max-width: 374px) {
         padding: 0 0 30px;
      }
   }

   &__buttons {
      display: flex;
      
   }

   &__button {
      border: 1px solid #D1D1E4;
      padding: 13px 21px;
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 13px;
      line-height: 16px;
      color: #ADB4D2;
      cursor: pointer;
   }

   &__button1 {
      color: #FFFFFF;
      background: #4C85FF;
      box-shadow: 0px 5px 15px rgba(188, 199, 255, 0.75);
      position: relative;
      padding: 13px 37px 13px 21px;
      &:hover {
         background: #4374df;
      }
      &::after {
         content: '';
         position: absolute;
         right: 23.5px;
         top: 19px;
         width: 0;
         height: 0;
         border-style: solid;
         border-width: 3.5px 0 3.5px 7px;
         border-color: transparent transparent transparent #ffffff;
      }
   }

   &__imgs {
   }

   &__img {
      position: absolute;
      top: 140px;
      right: 48px;
      @include adaptiveValue('width', 854, 653, 1440, 1023, 1);
      @include adaptiveValue('height', 736, 562, 1440, 1023, 1);
      @include adaptiveValue('top', 140, 188, 1279, 1023, 1);
      @include adaptiveValue('right', 24, -15, 1279, 1023, 1);
      @media(max-width: 1023px) {
         left: -90px;
         top: auto;
         right: auto;
         bottom: -157px;
      }
      @media(max-width: 767px) {
         width: auto;
         height: auto;
         left: -50px;
         top: auto;
         right: auto;
         bottom: -90px;
      }
      @media(max-width: 424px) {
         bottom: -79px;
      }
      @media(max-width: 374px) {
         bottom: -60px;
      }
   }
}