.main-screen {
   &__block {
      position: relative;
      overflow: hidden;
      background: linear-gradient(180deg, #F4F7FF 0%, #F1F5FF 100%); 
   }
}
.main-screen-slider {
   display: flex;
   transition: 0.5s;
   z-index: 2;
   position: relative;

   &__dots {
      display: flex;
      gap: 12px;
      position: absolute;
      bottom: 28px;
      left: calc(50% - 24px);
      z-index: 3;
      @media(max-width: 767px) {
         bottom: 40px;
      }
   }

   &__dot {
      width: 8px;
      height: 8px;
      background: #D1D6E3;
      border-radius: 50%;
      cursor: pointer;
      &.active {
         background: #5283FF;
      }
   }
   // transform: translateX(-500px);
}
.main-screen-slider-item {
   flex-shrink: 0;
   width: 100vw;
   height: 570px;
   position: relative;
   overflow: hidden;
   
   @media(max-width: 767px) {
      padding: 0 15px;
      height: 490px;
   }
   &__block {
      display: flex;
      align-items: center;
      flex-direction: column;
   }

   &__bg {
      position: absolute;
      bottom: 25px;
      svg {
         stroke-width: 3;
         z-index: 8;
         width: 100%;

      }

      @media (max-width: 1024px) {
         svg {
            stroke-width: 5;
         }
      }

      @media (max-width: 767px) {
         display: none;
         svg {
            stroke-width: 8;
         }
      }
   }

   &__dots {
      @media (max-width: 767px) {
         display: none;
      }
   }

   &__dot {
      width: 15px;
      height: 15px;
      background: #FFFFFF;
      border: 2px solid #5E80FE;
      border-radius: 50%;
      box-shadow: 0px 0px 15px rgba(82, 131, 255, 0.25);
      position: absolute;
      cursor: pointer;
      &:hover {
         &::after {
            display: block;
         }
      }
      &::after {
         content: '';
         width: 130px;
         height: 102px;
         background: url('../img/main-screen/union.png') 0 0/ 100% 100% no-repeat;
         display: none;
         margin: -90px 0 0 -60px;
      }
   }

   &__dot1 {
      @include adaptiveValue('bottom', 5, 74, 1440, 767, 1);
      @include adaptiveValue('left', 100, 56, 1440, 767, 1);
      
   }

   &__dot2 {
      @include adaptiveValue('bottom', 34, 85, 1440, 767, 1);
      @include adaptiveValue('left', 508, 241, 1440, 767, 1);
      
   }

   &__dot3 {
      @include adaptiveValue('bottom', 175, 166, 1440, 767, 1);
      @include adaptiveValue('right', 509, 260, 1440, 767, 1);
      
   }

   &__dot4 {
      @include adaptiveValue('bottom', 290, 225, 1440, 767, 1);
      @include adaptiveValue('right', 100, 52, 1440, 767, 1);
      
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
      text-align: center;
      padding: 92px 0 0;
      z-index: 2;
      @media(max-width: 767px) {
         padding: 60px 0 0;
      }
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 300;
      font-size: 48px;
      line-height: 60px;
      text-align: center;
      color: #3D4F9F;
      text-align: center;
      padding: 16px 0 28px;
      z-index: 2;
      span {
         font-weight: 500;
      }
      @media (max-width: 767px) {
         font-size: 42px;
         line-height: 45px;
         padding: 12px 0 22px;
      }
   }

   &__des {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 29px;
      text-align: center;
      letter-spacing: 0.03em;
      color: #8D96BD;
      text-align: center;
      padding: 0 0 20px;
      width: 502px;
      z-index: 2;
      @media(max-width: 767px) {
         width: auto;
      }
   }

   &__button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 52px;
      height: 52px;
      background: #FFFFFF;
      box-shadow: 0px 10px 50px rgba(201, 209, 231, 0.6);
      border-radius: 50%;
      cursor: pointer;
      z-index: 2;
      &::after {
         content: '';
         display: block;
         width: 0;
         height: 0;
         border-style: solid;
         border-width: 5px 0 5px 10px;
         border-color: transparent transparent transparent #5e81ff;

      }
   }
}

