.about {

   &__block {
      position: relative;
   }

   &__row {
      padding: 172px 0;
      @media(max-width: 1024px) {
         padding: 60px 25px 100px;
      }
      @media(max-width: 767px) {
         padding: 70px 25px;
      }
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
      text-align: center;
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      color: #3D4F9F;
      padding: 20px 0 92px;
      @media(max-width: 1023px) {
         padding: 20px 0 70px;
      }
      @media(max-width: 767px) {
         padding: 20px 0 50px;
      }
   }

   &__items {
      display: grid;
      grid-template: auto / repeat(4, 1fr);
      gap: 67px;
      @media(max-width: 767px) {
         grid-template: repeat(2, auto) / repeat(2, 1fr);
         gap: 30px;
      }
      @media(max-width: 374px) {
         grid-template: repeat(4, auto) / 1fr;
         gap: 45px;
      }
   }
}
.about-item {
   display: flex;
   flex-direction: column;
   align-items: center;
   &__img {
      background: #F1F5FF;
      width: 69px;
      height: 69px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
   }

   &__title {
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 25px;
      text-align: center;
      color: #4074F8;
      padding: 33px 0 25px;
      @media(max-width: 374px) {
         padding: 15px 0;
      }
   }

   &__subtitle {
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 600;
      font-size: 11px;
      line-height: 19px;
      text-align: center;
      color: #ADB3CB;
   }
}
.about-buttons {
   display: flex;
   justify-content: center;
   align-items: center;
   padding: 92px 0 0;
   gap: 47px;
   @media(max-width: 1023px) {
      padding: 70px 0 0;
   }
   @media(max-width: 374px) {
      flex-direction: column;
      gap: 15px;
      padding: 60px 0 0;
   }
   &__button {
      border-radius: 2px;
      padding: 13px 15px;
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: 0.2s;
   }

   &__red {
      background: #FFD7D7;
      color: #D43F3F;

      &:hover {
         background: #ffbbbb;
      }
   }

   &__blue {
      background: #D2E1FF;
      color: #3A79FF;
      &:hover {
         background: #b9cef8;
      }
   }

   &__text {
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 600;
      font-size: 11px;
      line-height: 19px;
      color: #ADB3CB;
      position: relative;
      &::after {
         content: '';
         display: block;
         position: absolute;
         left: 31px;
         top: 8px;
         width: 15px;
         height: 2px;
         background: #DAE4FE;
      }
      &::before {
         content: '';
         display: block;
         position: absolute;
         right: 31px;
         top: 8px;
         width: 15px;
         height: 2px;
         background: #DAE4FE;
      }
   }
}
.about-clouds {

   &__cloud {
      box-shadow: 0px 5px 45px rgba(151, 175, 227, 0.1);
      border-radius: 50% 50% 0 0/100% 100% 0 0;
      position: absolute;
      z-index: -1;
   }

