.start {

   &__block {
      background: #F5F8FF;
   }

   &__row {
      position: relative;
      padding: 104px 0 213px;
      @media(max-width: 1023px) {
         padding: 65px 25px 100px;
      }
      @media(max-width: 767px) {
         padding: 65px 25px 100px;
      }
      @media(max-width: 424px) {
         padding: 65px 15px 100px;
      }
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
      text-align: center;
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      color: #3D4F9F;
      padding: 20px 0 92px;
      @media(max-width: 1023px) {
         padding: 20px 0 50px;
      }
   }
}
.start-slider {
   overflow: hidden;
   &__arrowleft {
      position: absolute;
      top: calc(50% - -9.5px);
      left: -90px;
      cursor: pointer;
      @media(max-width: 1279px) {
         top: auto;
         left: 190px;
         bottom: 91px;
      }
      @media(max-width: 1023px) {
         bottom: 33px;
      }
      @media(max-width: 767px) {
         left: 90px;
      }
      @media(max-width: 374px) {
         left: 60px;
      }
   }

   &__items {
      display: flex;
      transition: 0.5s;
   }

   &__arrowright {
      position: absolute;
      top: calc(50% - -9.5px);
      right: -90px;
      cursor: pointer;
      @media(max-width: 1279px) {
         top: auto;
         right: 190px;
         bottom: 91px;
      }
      @media(max-width: 1023px) {
         bottom: 33px;
      }
      @media(max-width: 767px) {
         right: 90px;
      }
      @media(max-width: 374px) {
         right: 60px;
      }
   }

   &__dots {
      display: flex;
      gap: 12px;
      position: absolute;
      bottom: 104px;
      left: calc(50% - 24px);
      z-index: 3;
      @media(max-width: 1023px) {
         bottom: 45px;
      }
   }

   &__dot {
      width: 8px;
      height: 8px;
      background: #D1D6E3;
      border-radius: 50%;
      cursor: pointer;
      &.active {
         background: #5283FF;
      }
   }
}
.start-slider-item {
   padding: 64px 35px;
   display: flex;
   gap: 67px;
   background: #FFFFFF;
   border: 1px solid #D8E3FE;
   box-shadow: 0px 15px 50px #EFF4FF;
   flex-shrink: 0;
   width: 100%;
   @media(max-width: 1023px) {
      flex-direction: column;
   }
   @media(max-width: 767px) {
      gap: 25px;
      padding: 35px 25px;
   }
   @media(max-width: 424px) {
      padding: 35px 15px;
   }
   &__text {
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 400;
      font-size: 36px;
      line-height: 45px;
      color: #3D4F9F;
      @media(max-width: 767px) {
         font-size: 30px;
      }
      @media(max-width: 374px) {
         font-size: 24px;
      }
   }

   &__subtitle {
      width: 408px;
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      letter-spacing: 0.03em;
      color: #8D96BD;
      padding: 32px 0;
      @media(max-width: 1023px) {
         width: auto;
         padding: 25px 0;
      }
      @media(max-width: 374px) {
         padding: 15px 0 25px;
      }
   }
}
.start-slider-item-imgs {
   display: grid;
   grid-template: repeat(2, 126.58px) / repeat(3, 1fr);
   gap: 13px;
   @media(max-width: 1023px) {
      grid-template: repeat(2, auto) / repeat(3, auto);
   }
   &__search {
      display: none;
      width: 65.02px;
      height: 64.95px;
      border-radius: 50%;
      position: absolute;
      top: calc(50% - 32.5px);
      left: calc(50% - 32.5px);
      background: url('../img/start/icon.png') #fff center no-repeat;
      z-index: 2;
      cursor: pointer;
   }
   &__img {
      position: relative;
      &:hover {
         .start-slider-item-imgs__search {
            display: block;
         }
         &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(13.49deg, rgba(53, 170, 255, 0.68) 11.59%, rgba(90, 136, 255, 0) 96.88%)
         }
      }
      img {
         width: 100%;
         height: 100%;
      }
   }
   &__img1 {
      grid-row: 2 span;
      grid-column: 2 span;
   }
   &__img2 {
   }
   &__img3 {
   }
}
.start-slider-item-buttons {
   display: flex;
   gap: 8px;
   @media(max-width: 374px) {
      flex-direction: column;
   }
   &__button {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 13px 16px;
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 16px;
      border-radius: 2px;
      cursor: pointer;
      transition: 0.5s;
   }

   &__blue {
      background: #D2E1FF;
      color: #3A79FF;
      &:hover {
         background: #b9cef8;
      }
   }

   &__transparent {
      color: #A6B6DA;
      background: #FFFFFF;
      &:hover {
         background: #f0f0f0;
      }
   }
}
