.get-started {

		&__block {
			background: linear-gradient(229.32deg, #6888FF 12.77%, #5E80FF 73.11%);
			position: relative;
		}

		&__row {
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 91px 0;
			@media(max-width: 767px) {
				padding: 91px 25px;
			}
			@media(max-width: 424px) {
				padding: 70px 25px;
			}
		}

		&__subtitle {
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 500;
			font-size: 10px;
			line-height: 12px;
			
			/* identical to box height */
			text-align: center;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			
			color: #FFFFFF;
			position: relative;
			z-index: 2;
			
			
		}

		&__title {
			padding: 16px 0 28px;
			font-family: 'Quicksand';
			font-style: normal;
			font-weight: 300;
			font-size: 48px;
			line-height: 60px;
			text-align: center;
			color: #FFFFFF;
			position: relative;
			z-index: 2;
			span {
				font-weight: 500;
			}
			@media(max-width: 424px) {
				font-size: 42px;
				padding: 10px 0 15px;
			}
			@media(max-width: 374px) {
				font-size: 36px;
			}
		}

		&__des {
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 400;
			font-size: 15px;
			line-height: 29px;
			text-align: center;
			letter-spacing: 0.03em;
			
			color: #FFFFFF;
			width: 712px;
			padding: 0 0 28px;
			position: relative;
			z-index: 2;
			@media(max-width: 767px) {
				width: auto;
			}
		}

		&__buttons {
			display: flex;
			justify-content: center;
		}

		&__button {
			display: flex;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			font-family: 'Quicksand';
			font-style: normal;
			font-size: 13px;
			line-height: 16px;
			transition: 0.5s;
			position: relative;
			z-index: 2;
		}

		&__button1 {
			color: #565F82;
			background: #FFFFFF;
			box-shadow: 0px 5px 15px #6480E8;
			font-weight: 700;
			padding: 13px 17px;
			&:hover {
				background: #e2e1e1;
			}
		}

		&__button2 {
			color: #C7D3FF;
			border: 1px solid #A0B4FF;
			padding: 13px 8px;
		}
}
