.stats {

   &__block {
   }

   &__row {
      display: flex;
      @media(max-width: 767px) {
         flex-direction: column;
      }
   }
}
.stats-right {
   background: linear-gradient(229.32deg, #6888FF 12.77%, #5E80FF 73.11%);
   width: 50%;
   padding: 66px 90px 66px 0;
   display: flex;
   align-items: flex-end;
   flex-direction: column;
   @media(max-width: 767px) {
      width: 100%;
      padding: 40px 40px 40px 0;
   }
   &__img {
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 400;
      font-size: 48px;
      line-height: 83px;
      letter-spacing: 0.03em;
      color: #FFFFFF;
      padding: 5px 0;
   }

   &__subtitle {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      text-align: right;
      letter-spacing: 0.03em;
      color: #FFFFFF;
      width: 260px;
   }
}
.stats-left {
   background: linear-gradient(101.48deg, #E9EFFF 41.56%, #E0E9FF 81.53%);
   width: 50%;
   padding: 66px 0 66px 90px;
   display: flex;
   align-items: flex-start;
   flex-direction: column;
   @media(max-width: 767px) {
      width: 100%;
      padding: 40px 0 40px 40px;
   }
   
   &__img {
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 400;
      font-size: 48px;
      line-height: 83px;
      letter-spacing: 0.03em;
      color: #5E80FF;
      padding: 5px 0;
   }

   &__subtitle {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      letter-spacing: 0.03em;
      color: #5E80FF;
      width: 260px;
   }
}
