.quotes {

   &__block {
      background: linear-gradient(180deg, #EAF0FF 0%, #F6F9FF 100%);
      position: relative;
   }

   &__row {
      display: flex;
      padding: 109px 0 183px;
      @media(max-width: 1023px) {
         flex-direction: column;
         padding: 65px 25px 100px;
      }
      @media(max-width: 374px) {
         padding: 65px 25px 80px;
      }
   }

   &__text {
      padding: 0 132px 0 0;
      position: relative;
      z-index: 2;
      @media(max-width: 1023px) {
         padding: 0;
      }
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #3D4F9F;
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 500;
      font-size: 36px;
      line-height: 45px;
      color: #3D4F9F;
      padding: 31px 0 14px;
      width: 333px;
      @media(max-width: 374px) {
         width: auto;
         font-size: 26px;
         padding: 15px 0 10px;
      }
   }

   &__des {
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 23px;
      letter-spacing: 0.03em;
      color: #A4ABC8;
      width: 323px;
      @media(max-width: 1023px) {
         padding: 0 0 30px 0;
      }
      @media(max-width: 374px) {
         width: auto;
         padding: 0 0 25px 0;
      }
   }
}
.quotes-slider {
   padding: 32px 0;
   overflow: hidden;
   margin-top: -32px;
   min-width: 1000px;
   @media(max-width: 1023px) {
      min-width: auto;
   }
   &__items {
      display: flex;
      transition: 0.5s;
   }

   &__dots {
      display: flex;
      gap: 12px;
      position: absolute;
      bottom: 104px;
      right: 32%;
      z-index: 3;
      @media(max-width: 1023px) {
         right: calc(50% - 84px);
         bottom: 45px;
      }
      
      @media(max-width: 374px) {
         bottom: 35px;
      }
   }

   &__dot {
      width: 8px;
      height: 8px;
      background: #D1D6E3;
      border-radius: 50%;
      cursor: pointer;
      &.active {
         background: #5283FF;
      }
   }
}
.quotes-slider-item {
   flex-shrink: 0;
   padding: 0 60px 0 0;
   &.active {
      .quotes-slider-item__row {
         opacity: 1;
      }
   }
   &__row {
      padding: 40px 70px 0;
      height: 175.37px;
      background: #FFFFFF;
      border: 1px solid #D8E3FE;
      box-shadow: 0px 5px 50px #F3F5FA;
      opacity: 0.5;
      position: relative;
      @media(max-width: 767px) {
         padding: 20px 40px 40px;
      }
      @media(max-width: 374px) {
         padding: 20px 20px 40px;
      }
   }

   &__text {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.03em;
      color: #676F95;
      width: 283px;
      @media(max-width: 424px) {
         width: 244px;
      }
      @media(max-width: 374px) {
         width: 238px;
      }
   }

   &__img {
      position: absolute;
      bottom: -31px;
      left: calc(50% - 31px);
      z-index: 2;
   }
}
.active {
}
