.signup {

   &__block {
      background: #FCFDFF;
   }

   &__row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 118px 0;
      @media(max-width: 1023px) {
         padding: 80px 25px 60px;
         flex-direction: column;
         align-items: flex-start;
         gap: 40px;
      }
      @media(max-width: 767px) {
         gap: 25px;
      }
   }

   &__text {
   }

   &__title {
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      
      color: #3D4F9F;
      
      padding: 0 0 18px 0;      
   }

   &__subtitle {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      letter-spacing: 0.03em;
      width: 330px;
      color: #8D96BD;
      @media(max-width: 374px) {
         width: auto;
      }
   }

   &__form {
      display: flex;
      gap: 14px;
      @media(max-width: 767px) {
         flex-direction: column;
      }
   }

   &__input {
      width: 279px;
      height: 59px;
      background: #EFEFF7;
      border-radius: 5px;
      padding: 21px 0 21px 23px;
      border: 0;
      @media(max-width: 374px) {
         width: 270px;
      }
   }

   &__button {
      background: #4D6CE1;
      box-shadow: 0px 5px 15px rgba(116, 104, 255, 0.2);
      border-radius: 5px;
      padding: 21px 62px;
      width: 184px;
      height: 59px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 16px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #FFFFFF;
      cursor: pointer;
      &:hover {
         background: #4562c9;
      }
   }
}