.header {

   &__block {
   }

   &__container {
      max-width: 1206px;
      margin: 0 auto;
   }

   &__row {
      padding: 19px 0;
      display: flex;
      justify-content: space-between;
      @media(max-width: 1250px) {
         padding: 19px 25px;
      }
      @media(max-width: 767px) {
         padding: 19px 10px;
      }
   }

   &__logo {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background: #5E81FE;
      cursor: pointer;
   }

   &__right {
      display: flex;
      gap: 44px;
      align-items: center;
   }

   &__button {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D2E1FF;
      border-radius: 2px;
      padding: 13px 0;
      width: 113px;
      cursor: pointer;
      transition: 0.2s;
      &:hover {
         background: #b9cef8;
      }
      a {
         font-family: 'Quicksand';
         font-style: normal;
         font-weight: 700;
         font-size: 13px;
         color: #3A79FF;
         line-height: 16px;
         text-decoration: none;
      }
   }
}

.header-socials {
   display: flex;
   gap: 34px;
   cursor: pointer;
   @media(max-width: 767px) {
      display: none;
   }
   &__item {
   }

   &__icon1 {
      width: 8.71px;
      height: 17px;
      background: url('../img/header/socials1.png');
      &:hover {
         background: url('../img/header/fillSocials1.png');
      }
   }

   &__icon2 {
      width: 16.7px;
      height: 13.8px;
      background: url('../img/header/socials2.png');
      &:hover {
         background: url('../img/header/fillSocials2.png');
      }
   }

   &__icon3 {
      width: 16.51px;
      height: 14.8px;
      background: url('../img/header/socials3.png');
      &:hover {
         background: url('../img/header/fillSocials3.png');
      }
   }

   &__icon4 {
      width: 16.51px;
      height: 12.86px;
      background: url('../img/header/socials4.png');
      &:hover {
         background: url('../img/header/fillSocials4.png');
      }
   }
}
